import React, { useState } from 'react';
import { observer } from "mobx-react";

import styled from 'styled-components';
import '../css/UploadTool.css';

import useStore from 'stores/useStore';

import FabricService from '../services/FabricService';

import SpinnerButton from './SpinnerButton';


const Div = styled.div`
  display: flex;
  margin-top: 20px;
	
  @media (min-width: 700px) {
    width: 100%;
    height: 100%;
  }
`;

const Input = styled.input`
`;


const SimilarityTool = () => {

	const { fabricStore } = useStore();
	
	// 담을 state
	const [selectedFile, setSelectedFile] = useState(null);
	const [isButtonOn, setIsButtonON] = useState(false);
	

	const getBase64 = (file) => {
		let reader = new FileReader()
		reader.readAsDataURL(file)
		reader.onload = () => {
			fabricStore.setSelectedImg(reader.result);
			// alert(JSON.stringify(reader.result));
		};
		reader.onerror = function (error) {
			console.log('Error: ', error);
		}
	}
	
	// onChange역할
	const handleFileChange = (event) => {
		setSelectedFile(event.target.files[0]);
		getBase64(event.target.files[0])
		
		
	};
 
	// formData라는 instance에 담아 보냄
	const handleFileUpload = () => {
    const formData = new FormData();

		if(selectedFile === null) {
			return alert('사진 파일을 선택해주세요.')
		}

		formData.append("file", selectedFile, selectedFile.name);
		
		setIsButtonON(true);
		FabricService.similarityImage(formData).then(res => {
			console.log(res);
			fabricStore.setFabricList(res['data'][0]);
		}).catch(e => {
			console.log(e);
		}).finally(() => {
			setIsButtonON(false);
		});
  };

  

  return (
	<Div>
		<div className="container">
			<div className="row" style={{textAlignLast: "center", padding: "20px"}}>
				<h1 style={{fontSize: "25px", padding: "10px"}}>디자인 유사도 검사</h1>
				<p style={{fontSize: "15px"}}>자신이 보유한 이미지에 대해 Fabric Dive에서 보유한 이미지와 유사성을 판단하는 서비스 입니다.</p>
				<p style={{fontSize: "15px"}}>이 서비스를 통해, 이미지 유사성을 판단해 보실 수 있습니다.</p>

			</div>
			<div className="row">
				<div className="col-sm-12">
					<div className="form-group files color">
						<label>이미지 파일 선택</label>
						<div className="row">
							<div className="col-sm-8">
								<input type="file" className="form-control" name="file" accept="image/*" onChange={handleFileChange.bind(this)}/>
								{/* <input type="file" name="file" onChange={handleFileChange.bind(this)}/> */}
							</div>
							<div className="col-sm-4">
								<SpinnerButton click={handleFileUpload} disabled={isButtonOn} name={'검색'} />
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</Div>
)};

export default SimilarityTool;







